import React from 'react';
import { Container, Typography } from '@mui/material';

function CopyrightNote() {
  return (
    <Container maxWidth="lg" sx={{ textAlign: 'center', padding: '20px' }}>
      <Typography variant="h5" gutterBottom>
        Copyright  LiquorBizConnect Developer 2024. All rights reserved.
      </Typography>
      <Typography variant="body1">
        The data displayed on this website is sourced from the Connecticut Data API. We do not collect any personal data or use this information for commercial purposes. This service is provided solely to assist residents of Connecticut in finding relevant business information. We are committed to transparency and privacy, ensuring that your data remains secure and confidential.
      </Typography>
    </Container>
  );
}

export default CopyrightNote;
