import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { AppBar, Toolbar, IconButton, Typography, Drawer, List, ListItem, ListItemText, Tabs, Tab, useMediaQuery, useTheme } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import LiquorStoresPage from './components/LiquorStores/LiquorStoresPage';
import CopyrightNote from './components/Copyright/CopyrightNote';
import HomePage from './components/Home/HomePage';
import BusinessProfitCalculator from './components/BusinessProfitCalc/BusinesProfitCalculator';
import './App.css';

function App() {
  const [value, setValue] = useState(0);
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleChange = (event, newValue) => {
    setValue(newValue);
    setIsDrawerOpen(false); // Close drawer when a tab is selected
  };

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setIsDrawerOpen(open);
  };

  const menuItems = ['Home', 'Active Business Search', 'Business Profit Calculator', 'Copyright Note'];

  return (
    <Router>
      <div className="App">
        {/* AppBar with Hamburger Icon */}
        <AppBar position="static">
          <Toolbar>
            {isMobile ? (
              <IconButton
                edge="start"
                color="inherit"
                aria-label="menu"
                onClick={toggleDrawer(true)}
              >
                <MenuIcon />
              </IconButton>
            ) : (
              <Tabs value={value} onChange={handleChange}>
                <Tab label="Home" />
                <Tab label="Active Business Search" />
                <Tab label="Business Profit Calculator" />
                <Tab label="Copyright Note" />
              </Tabs>
            )}
            <Typography variant="h6" sx={{ flexGrow: 1 }}>
              LiquorBizConnect
            </Typography>
          </Toolbar>
        </AppBar>

        {/* Drawer for Mobile View */}
        {isMobile && (
          <Drawer anchor="left" open={isDrawerOpen} onClose={toggleDrawer(false)}>
            <List sx={{ width: 250 }}>
              {menuItems.map((text, index) => (
                <ListItem button key={text} onClick={() => handleChange(null, index)}>
                  <ListItemText primary={text} />
                </ListItem>
              ))}
            </List>
          </Drawer> 
        )}

        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/active-business-search" element={<LiquorStoresPage />} />
          <Route path="/business-profit-calculator" element={<BusinessProfitCalculator />} />
          <Route path="/copyright-note" element={<CopyrightNote />} />
        </Routes>

        {/* Main Content */}
        {value === 0 && <HomePage />}
        {value === 1 && <LiquorStoresPage />}
        {value === 2 && <BusinessProfitCalculator />}
        {value === 3 && <CopyrightNote />}
      </div>
    </Router>    
  );
}

export default App;
