import React from 'react';
import { Box, Typography, Button } from '@mui/material';
import TextLoop from "react-text-loop";
import { useNavigate } from 'react-router-dom';
import './HomePage.css'; // For custom styles

function HomePage() {
  const navigate = useNavigate();

  const handleButtonClick = () => {
    navigate('/business-profit-calculator');
  };

  return (
    <Box
      sx={{
        height: '100vh',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        backgroundColor: '#f0f0f0', // Use a light gray background
        color: '#333',
        textAlign: 'center',
      }}
    >
      <Typography 
        variant="h2" 
        gutterBottom 
        className="bounce" // Apply bounce animation class here
      >
        Welcome to Connecticut's Liquor Store Guide
      </Typography>
      <TextLoop interval={3000}>
        <Typography variant="h4">Find the Active Liquor Stores</Typography>
        <Typography variant="h4">Make Informed Business Decisions</Typography>
        <Typography variant="h4">Connecticut's Trusted Guide</Typography>
      </TextLoop>
      <Typography variant="body1" sx={{ marginTop: 2 }}>
        Explore liquor stores across Connecticut and make informed decisions about buying or selling businesses using Business Profit Calculator.
      </Typography>

      {/* Animated Button */}
      <Button 
        variant="contained" 
        color="primary" 
        size="large" 
        sx={{
          marginTop: 3,
          padding: '10px 20px',
          fontSize: '1.5rem',
          transition: 'transform 0.3s ease, box-shadow 0.3s ease',
          '&:hover': {
            transform: 'scale(1.05)', // Scale up on hover
            boxShadow: '0px 4px 15px rgba(0, 0, 0, 0.3)', // Add shadow on hover
          },
        }}
        onClick={handleButtonClick} // Navigate on click
      >
        Go to Business Profit Calculator
      </Button>
    </Box>
  );
}

export default HomePage;
