import React, { useState, useEffect } from 'react';
import { 
  Container, 
  Typography, 
  TextField, 
  Grid, 
  Paper, 
  Box, 
  IconButton, 
  Collapse,
  Tooltip
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline'; // Import Help Icon

const CT_SALES_TAX_RATE = 0.0635; // 6.35% Connecticut sales tax

function BusinessProfitCalculator() {
  const [income, setIncome] = useState({
    grossSale: 0,
    atmIncome: 0,
    lottoIncome: 0
  });

  const [costOfGoods, setCostOfGoods] = useState(0);
  
  const [expenses, setExpenses] = useState({
    accountantFee: 0,
    bankCharges: 0,
    cableSecurity: 0,
    propertyTax: 0,
    stateLicenseFee: 0,
    garbage: 0,
    creditCardFee: 0,
    insurance: 0,
    officeSupply: 0,
    rentLease: 0,
    repairMaintenance: 0,
    utilitiesSecurity: 0,
    mortgageYearly: 0,
    otherExpenses: 0
  });

  const [showAdditionalExpenses, setShowAdditionalExpenses] = useState(false);
  
  const [results, setResults] = useState({
    totalIncome: 0,
    salesTax: 0,
    grossIncome: 0,
    totalExpenses: 0,
    netProfit: 0,
    profitPercentage: 0
  });

  useEffect(() => {
    calculateProfit();
  }, [income, costOfGoods, expenses]);

  const handleIncomeChange = (e) => {
    setIncome({ ...income, [e.target.name]: parseFloat(e.target.value) || 0 });
  };

  const handleCostOfGoodsChange = (e) => {
    setCostOfGoods(parseFloat(e.target.value) || 0);
  };

  const handleExpenseChange = (e) => {
    setExpenses({ ...expenses, [e.target.name]: parseFloat(e.target.value) || 0 });
  };

  const calculateProfit = () => {
    const totalIncome = income.grossSale + income.atmIncome + income.lottoIncome;
    const salesTax = income.grossSale * CT_SALES_TAX_RATE;
    const grossIncome = totalIncome - costOfGoods - salesTax;
    
    const totalExpenses = Object.values(expenses).reduce((sum, expense) => sum + expense, 0);
    const netProfit = grossIncome - totalExpenses;

    let profitPercentage = (totalIncome > 0) ? (netProfit / totalIncome) * 100 : 0;

    setResults({
      totalIncome,
      salesTax,
      grossIncome,
      totalExpenses,
      netProfit,
      profitPercentage
    });
  };

 return (
   <Container maxWidth="lg">
     <Typography 
       variant="h4" 
       gutterBottom 
       sx={{
         fontFamily: 'Roboto',
         color: '#2C3E50',
         fontWeight: 'bold',
         textAlign: 'center',
         marginBottom: '16px'
       }}
     >
       Business Profit Calculator
     </Typography>
     <Grid container spacing={3}>
       <Grid item xs={12} md={6}>
         <Paper elevation={3} sx={{ p: 2 }}>
           <Typography variant="h6" gutterBottom>Income</Typography>
           <TextField fullWidth size="small" label="Gross Sale" name="grossSale" type="number" onChange={handleIncomeChange} margin="dense" />
           <TextField fullWidth size="small" label="ATM Income" name="atmIncome" type="number" onChange={handleIncomeChange} margin="dense" />
           <TextField fullWidth size="small" label="Lotto Income" name="lottoIncome" type="number" onChange={handleIncomeChange} margin="dense" />
           
           <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>Cost of Goods</Typography>
           <TextField fullWidth size="small" label="Cost of Goods" type="number" onChange={handleCostOfGoodsChange} margin="dense" />
           
           <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>Expenses</Typography>
           {/* Top Expenses */}
           {['rentLease', 'creditCardFee', 'insurance', 'mortgageYearly', 'otherExpenses'].map((expense) => (
             <div key={expense} style={{ display: 'flex', alignItems: 'center' }}>
               <TextField 
                 fullWidth 
                 size="small"
                 label={expense.replace(/([A-Z])/g, ' $1').replace(/^./, (str) => str.toUpperCase())}
                 name={expense}
                 type="number"
                 onChange={handleExpenseChange}
                 margin="dense"
               />
               {expense === 'creditCardFee' && (
                 <Tooltip title="The credit card fee is approximately **2.5%** of the total credit card charges in gross sales.">
                   <IconButton size="small">
                     <HelpOutlineIcon fontSize="small" />
                   </IconButton>
                 </Tooltip>
               )}
             </div>
           ))}

           {/* Toggle for Additional Expenses */}
           <IconButton onClick={() => setShowAdditionalExpenses(!showAdditionalExpenses)}>
             {showAdditionalExpenses ? <ExpandLessIcon /> : <ExpandMoreIcon />}
           </IconButton>
           {showAdditionalExpenses && (
             <>
               {Object.keys(expenses).filter(expense => !['rentLease', 'creditCardFee', 'insurance', 'mortgageYearly', 'otherExpenses'].includes(expense)).map((expense) => (
                 <TextField 
                   key={expense}
                   fullWidth 
                   size="small"
                   label={expense.replace(/([A-Z])/g, ' $1').replace(/^./, (str) => str.toUpperCase())}
                   name={expense}
                   type="number"
                   onChange={handleExpenseChange}
                   margin="dense"
                 />
               ))}
             </>
           )}
         </Paper>
       </Grid>
       <Grid item xs={12} md={6}>
         <Paper elevation={3} sx={{ p: 2 }}>
           <Typography variant="h6" gutterBottom>Results</Typography>
           <Box sx={{ mb: 2 }}>
             <Typography>Total Income: ${results.totalIncome.toFixed(2)}</Typography>
             <Typography>CT Sales Tax (6.35%): ${results.salesTax.toFixed(2)}</Typography>
             <Typography>Gross Income: ${results.grossIncome.toFixed(2)}</Typography>
             <Typography>Total Expenses: ${results.totalExpenses.toFixed(2)}</Typography>
             <Typography variant="h6">Net Profit: ${results.netProfit.toFixed(2)}</Typography>
             <Typography variant="h6">Profit Percentage: {Math.round(results.profitPercentage)}%</Typography>
           </Box>
         </Paper>
       </Grid>
     </Grid>
   </Container>
 );
}

export default BusinessProfitCalculator;
